.home-page {
 
  background: #fff
;
}
.invite-page {
  /* карточка должна занять экран без зазоров */
  min-height: auto;

  /* фон пусть остаётся, но его не будет видно на мобилке,
     потому что карточка будет сама 100vh по высоте и шире */
  background: radial-gradient(circle at 50% 0%, #ffffff 0%, #f3f3f3 70%);

  display: flex;
 justify-content: center;
  align-items: flex-start;

  /* убираем верх/низ отступы совсем */
 padding: 40px 0 0;

  color: #1f1f1f;
  font-family: "Playfair Display", Georgia, serif;
}

/* карточка самой открытки */
.invite-card {
    position: relative;

  /* делаем шире, чтобы на телефоне фон не торчал по краям */
  width: 100%;
  max-width: 420px;

  /* высота = высота экрана */
  height: 120vh;

  background: #fff;
 
}

/* рамка и арка */
.invite-border {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.arc-text {
  font-family: "Playfair Display", serif;
  font-size: 10px;
  letter-spacing: 0.15em;
  fill: #7a1a1a;
  text-transform: uppercase;
}

/* контент */
.invite-content {
  position: absolute;
  top: 150px;
  width: 100%;
  text-align: center;
  padding: 0 20px;
}

/* имена */
.names {
  position: relative;
  height: 80px;
  margin-bottom: 30px;
  font-family: "Great Vibes", cursive;
  color: #1f1f1f;
}

.name-left {
  position: absolute;
  left: 18%;
  top: 0;
  font-size: 2.3rem;
}

.name-right {
  position: absolute;
  right: 18%;
  top: 35px;
  font-size: 2.3rem;
}

/* фото */
.photo-wrapper {
  border: 6px solid #7a1a1a;
  width: 100%;
  max-width: 250px;
  aspect-ratio: 4 / 5;
  margin: 0 auto 30px auto;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.25);
}

.photo-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

/* нижняя линия и дата */


.date {
  font-size: 12px;
  letter-spacing: 0.2em;
  text-align: right;
  margin-right: 30px;
  color: #2b2b2b;
}

/* адаптация под более широкие экраны (планшет/десктоп),
   чтобы на них не было безумной "растянутой на весь экран" карточки */
@media (max-width: 767px) {
  .invite-card {
    border-radius: 0;
    box-shadow: none;
    width: 100%;
    max-width: 100%;
    min-height: 90px;
    padding: 80px 0 10px;
    overflow: visible;
  }

  .invite-content {
    top: 120px; /* можно чуть ниже опустить текст */
  }

  .photo-wrapper {
    max-width: 280px;
    margin-bottom: 40px;
  }

  .date {
    font-size: 13px;
    margin-right: 20px;
    margin-top: 40px;
  }
}

