/* базовая страница */
.story-page {
  width: 100%;
  min-height: auto;
  background: #ffffff;
  color: #000000;
  display: flex;
  justify-content: center;
  padding: 40px 0 0;
  margin: 0;
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
}

/* контейнер */
.story-inner {
  max-width: 560px; /* было 520px, делаем чуть шире, но не ломаем мобильный */
  width: 100%;
  text-align: center;

  opacity: 0;
  transform: translateY(30px);
  transition: all 1s cubic-bezier(0.16, 1, 0.3, 1);
}

.story-visible {
  opacity: 1;
  transform: translateY(0);
}

/* рукописный заголовок */
.story-heading {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.2;
  color: #000000;
  margin-bottom: 24px;
}

/* основной текст */
.story-text {
  font-family: "Playfair Display", Georgia, serif;
  font-size: 1.3rem;
  line-height: 1.5;
  font-weight: 400;
  color: #000000;
  margin: 0 auto 24px;
  max-width: 480px;
}

/* дата крупно */
.story-date {
  font-family: "Playfair Display", Georgia, serif;
  font-size: 1.1rem;
  font-weight: 100;
  line-height: 1.7;
  color: #000000;
  margin-bottom: 32px;
  letter-spacing: 0.05em;
}

/* блок календаря в целом */
.calendar-block {
  text-align: center;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
  margin: 0 auto;
  color: #7a1a1a;
}

/* название месяца */
.calendar-month {
  font-size: 1rem;
  font-weight: 800;
  letter-spacing: 0.08em;
  margin-bottom: 16px;
  color: #7a1a1a;
}

/* карточка календаря */
.calendar-card {
  max-width: 380px;        /* шире самой таблички изначально */
  width: 100%;
  margin: 0 auto;

  background: #fff7f7;     /* очень деликатный розово-молочный фон */
  border: 1px solid #8a282855;
  border-radius: 16px;
  padding: 16px 20px 20px;

  box-shadow:
    0 18px 40px rgba(138, 40, 40, 0.08),
    0 2px 4px rgba(0,0,0,0.04);
}

/* сама таблица */
.calendar-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 1rem;          /* сделал крупнее */
  line-height: 1.4;
  font-weight: 400;
  color: #000000;
  table-layout: fixed;
}

/* строка с днями недели */
.calendar-head-row th {
  background-color: #8a2828;  /* бордовый бар */
  color: #ffffff;
  font-weight: 600;
  font-size: 0.75rem;
  line-height: 1.2;
  text-transform: lowercase;
  letter-spacing: 0.05em;

  padding: 10px 0;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
}

/* скругление только у крайних ячеек хэдера */
.calendar-head-row th:first-child {
  border-top-left-radius: 10px;
}
.calendar-head-row th:last-child {
  border-top-right-radius: 10px;
}

/* обычные дни */
.calendar-table td {
  width: calc(100% / 7);
  height: 44px;              /* было 32px → больше воздуха */
  text-align: center;
  vertical-align: middle;
  color: #000000;
  position: relative;
  font-size: 0.95rem;
  line-height: 44px;
  border-bottom: 1px solid rgb(0, 0, 0);

    border-right: 1px solid rgba(0,0,0,0.05);
}
.calendar-table td:last-child {
  border-right: 0;
}

/* финальная строка без грубой границы снизу у последнего ряда */
.calendar-table tbody tr:last-child td {
  border-bottom: 0;
}

/* ячейка с сердцем */
.calendar-heart-cell {
  padding: 0;
}

.heart-wrapper {
  position: relative;
  width: 38px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.heart-icon {
  position: absolute;
  width: 34px;
  height: 30px;
  top: 2px;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: block;
}

.heart-day {
  position: relative;
  z-index: 2;
  font-size: 0.8rem;
  line-height: 1;
  font-weight: 600;
  color: #ffffff;
  display: block;
  transform: translateY(2px);
}

/* мобильная адаптация */
@media (max-width: 480px) {
  .story-page {
    padding: -520px 0 0;
  }

  .story-heading {
    font-size: 1.6rem;
    margin-bottom: 8px;
  }

  .story-text {
    font-size: 0.95rem;
    line-height: 1.4;
    margin-bottom: 8px;
  }

  .story-date {
    font-size: 1rem;
    margin-bottom: 10px;
  }

  .calendar-month {
    font-size: 0.9rem;
    margin-bottom: 14px;
  }

  .calendar-card {
    max-width: 360px;
    padding: 14px 16px 16px;
    border-radius: 14px;
  }

  .calendar-table {
    font-size: 0.9rem;
  }

  .calendar-head-row th {
    font-size: 0.7rem;
    padding: 9px 0;
  }

  .calendar-table td {
    height: 40px;
    line-height: 40px;
    font-size: 0.85rem;
  }

  .heart-wrapper {
    width: 32px;
    height: 28px;
  }

  .heart-icon {
    width: 30px;
    height: 26px;
    top: 2px;
  }

  .heart-day {
    font-size: 0.75rem;
  }
}
